<template>
  <div class="home">    
    <div class="columns is-centered">
      <div class="column is-3 has-background-light">
        <div class="box">
          <section class="buttons">
            <button v-bind:class="{'button is-success': task=='mle', 'button': task!='mle'}"
                  @click="setTask('mle')">ML estimation</button>
            <button v-show="advanced" v-bind:class="{'button is-success': task=='lkhd', 'button': task!='lkhd'}"
                  @click="setTask('lkhd')">Compute likelihood</button>
            <div class="field" v-show="task=='mle'">
              <label class="label">Range of selection coefficient</label>
              <div class="control">
                <label class="label">Min</label>
                <input class="input is-info" type="text" v-model.number="smin">
                <label class="label">Max</label>
                <input class="input is-info" type="text" v-model.number="smax">
              </div>
              <br/>
              <button v-if="!workerBusy" class="button is-success" @click="gss">Estimate: </button>
              <button v-if="workerBusy" class="button is-danger">Calculating</button>
              <i v-if="workerBusy" class="fa fa-spinner fa-spin" style="font-size:36px;color:red"></i>
              <pre v-if="workerBusy">&nbsp; Step {{ gssStep }} of {{ gssInfo.n + 1}}</pre>
            </div>
            <div class="field" v-show="task=='lkhd'">
              <label class="label">selection coefficient</label>
              <div class="control">
                <input class="input is-info" type="text" v-model.number="sel">
              </div>
              <br/>
              <button class="button is-success" @click="computeLkhd(sel)">Compute</button>
            </div>
          </section>
          <hr>
          <h2 class="title">Parameters</h2>
          <section class="form">
            <div class="field">
              <div class="tooltip">
                <label class="label">N</label>
                <span class="tooltiptext">Reference population size</span>
              </div>
              <div class="control">
                <input class="input is-info" type="text" v-model.number="N0" data-tooltip="Tooltip Text">
              </div>
            </div>
            <div class="field">
              <div class="tooltip">
                <label class="label">h</label>
                <span class="tooltiptext">Dominance parameter</span>
              </div>
              <div class="control">
                <input class="input is-info" type="text" v-model.number="hh">
              </div>
            </div>
            <label class="checkbox"><input type="checkbox" v-model="advanced">&nbsp; Advanced options</label>
            <div class="field" v-show="advanced">
              <div class="tooltip">
                <label class="label">Likelihood cutoff ratio</label>
                <span class="tooltiptext">Stop evolution of KBE after likelihood has dropped to 1/lcr of the maximum</span>
              </div>
              <div class="control">
                <input class="input is-info" type="text" v-model.number="lkhdCutoffRatio">
              </div>
            </div>
            <div class="field" v-show="advanced">
              <div class="tooltip">
                <label class="label">Number of sub-steps per time step</label>
                <span class="tooltiptext">One time step is N generations. More sub-steps is safer but takes longer.</span>
              </div>
              <div class="control">
                <input class="input is-info" type="text" v-model.number="nSubSteps">
              </div>
            </div>
            <section class="buttons" v-show="advanced">
              <button v-bind:class="{'button is-success': flagAge==1, 'button': flagAge==0}"
                    @click="setflagAge(1)">Infer allele age</button>
              <button v-bind:class="{'button is-success': flagAge==0, 'button': flagAge==1}"
                    @click="setflagAge(0)">Selection coefficient only</button>
            </section>
          </section>
        </div>
      </div>
      <div class="column is-6 has-background-light">
        <div class="box">
          <sample-chart v-bind:samplePlotDict="samplePlotDict"> </sample-chart>
        </div>
        <div v-if="gssInfo" class="box">
          <gss-plot v-show="task=='mle'" v-bind:gssPlotDict="gssPlotDict" vbind:key="componentKey"> </gss-plot>
          <lkhd-plot v-show="flagAge==1" v-bind:lkhdPlotDict="lkhdPlotDict"> </lkhd-plot>
          </div>
      </div>
      <div class="column is-3 has-background-link-light">
        <h4 class="title">Data</h4>
        <div class="box">
          <h4 class="subtitle">Historical sample</h4>
          <div class="tooltip">
            <div class="file">
              <label class="file-label">
                <input class="file-input" type="file" @change="readFile($event, 1)">
                <span class="file-cta">
                  <span class="file-icon"><i class="fas fa-upload"></i></span>
                  <span class="file-label">Update</span>
                </span>
              </label>
            </div>
            <span class="tooltiptext">
              Plain text file should have 3 columns of numbers: time (gbp), sample size, mutant count.
              See <a href="@/../example/sample_bwd2.txt">example</a>
            </span>
          </div>
          <pre v-if="dataFile">File: {{ dataFile.name }}</pre>
          <br/><br/>
          <button v-bind:class="{'button is-success': !showCounts, 'button': showCounts}"
                  @click="flipShowCounts">Show</button>
          <table class="table is-fullwidth" v-if="showCounts">
            <thead>
              <th>Time</th>
              <th>Total count</th>
              <th>Mutant count</th>
            </thead>
            <tbody>
              <tr v-for="ct in countsTr" :key="ct">
                  <td>{{ ct[0] }}</td>
                  <td>{{ ct[1] }}</td>
                  <td>{{ ct[2] }}</td>
              </tr>
            </tbody>
          </table>
        </div>
        <div class="box">
          <h4 class="subtitle">Historical population size</h4>
          <div class="tooltip">
            <div class="file">
              <label class="file-label">
                <input class="file-input" type="file" @change="readFile($event, 2)">
                <span class="file-cta">
                  <span class="file-icon"><i class="fas fa-upload"></i></span>
                  <span class="file-label">Update</span>
                </span>
              </label>
            </div>
            <pre v-if="NtFile">File: {{ NtFile.name }}</pre>
            <span class="tooltiptext">Plain text file should have 2 columns of numbers: time (gbp), population size.
              See <a href="@/../example/Nt_bwd1.txt">example</a>
            </span>
          </div>
          <br/><br/>
          <button v-bind:class="{'button is-success': !showNt, 'button': showNt}"
                  @click="flipShowNt">Show</button>
          <br/><br/>
          <button class='button' @click="removeNt">Remove</button>
          <table class="table is-fullwidth" v-if="showNt">
            <thead>
              <th>Time</th>
              <th>Population size</th>
            </thead>
            <tbody>
              <tr v-for="nt in NtTr" :key="nt">
                  <td>{{ nt[0] }}</td>
                  <td>{{ nt[1] }}</td>
              </tr>
            </tbody>
          </table>
        </div>
        <div class="box">
          <table class="table is-fullwidth tooltip">
            <span class="tooltiptext">CI for illustration only. For better results, use bootstrap.</span>
            <thead>
              <th></th>
              <th>Estimate</th>
              <th>95% CI
              </th>
            </thead>
            <tbody>
              <tr>
                <td>s</td>
                <td v-if="sEst">{{ sEst.mle.toFixed(4) }}</td>
                <td v-if="sEst">[{{ sEst.lower.toFixed(4) }}, {{ sEst.upper.toFixed(4) }}]</td>
              </tr>
              <tr>
                <td>allele age</td>
                <td v-if="ageEst">{{ ageEst.mle.toFixed(0) }}</td>
                <td v-if="ageEst">[{{ ageEst.upper.toFixed(0) }}, {{ ageEst.lower.toFixed(0) }}]</td>
              </tr>           
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import GssPlot from '@/components/GssPlot.vue'
import LkhdPlot from '@/components/LkhdPlot.vue'
import SampleChart from '@/components/SampleChart.vue'
//import SamplePlot from './components/SamplePlot.vue'

export default {
  name: 'Home',
  mounted() {
    this.worker = new Worker('@/../script/kbe/worker.js');
    this.worker.onmessage = (message) => {
      if (message.data.type === 'RESULT') {
        this.summary = message.data.payload.summary;
        this.lkhdt = message.data.payload.res;
        this.workerBusy = false;
      }
    }
    this.genSamplePlot();
  },
  data () {
    return {
      task: 'mle',
      advanced: false,
      summary: null, // array: [s, nt, allele age in generations, lkhd]
      lkhdt: null,
      sel: 0.01,
      smin: 0,
      smax: 0.02,
      N0: 5000,
      hh: 0.5,
      flagAge: 1,
      lkhdCutoffRatio: 5,
      nSubSteps: 20,
      dataFile: null,
      NtFile: null,
      counts: [ [ 0, 60, 121, 181, 241, 301, 362, 422, 482, 542, 603, 663, 723, 783, 844, 904, 964, 1024, 1085, 1145],
        [ 60, 60, 60, 60, 60, 60, 60, 60, 60, 60, 60, 60, 60, 60, 60, 60, 60, 60, 60, 60 ],
        [ 53, 49, 48, 36, 41, 28, 28, 23, 19, 7, 11, 9, 5, 3, 4, 2, 2, 1, 0, 0 ] ],
      Nt: [[0], [5000]], //[ [ 0, 484, 984 ], [ 8000, 4000, 8000 ] ],
      showCounts: false,
      showNt: false,
      workerBusy: false,
      sComputed: [], // selection coefficients
      lComputed: [], // likelihood values
      worker: null,
      gssInfo: null,
      gssStep: 0,
      samplePlotDict: [],
      lkhdPlotDict: {series: []},
      gssPlotDict: null,
      ageEst: null,
      sEst: null,
    }
  },
  components: {
    GssPlot,
    LkhdPlot,
    SampleChart,
  },
  computed: {
    dt: function() {
      return 1 / this.N0;
    },
    countsTr: function() {
      if (this.counts)
        return this.counts[0].map((_, colIndex) => this.counts.map(row => row[colIndex]));
      else
        return null;
    },
    NtTr: function() {
      if (this.Nt)
        return this.Nt[0].map((_, colIndex) => this.Nt.map(row => row[colIndex]));
      else
        return null;
    },
  },
  watch: {
    gssStep: function() {
      if (this.gssInfo.cur == 'c') {
        this.computeLkhd(this.gssInfo.c);
      } else if (this.gssInfo.cur == 'd') {
        this.computeLkhd(this.gssInfo.d);
      } else if (this.gssInfo.cur == 'a') {
        this.computeLkhd(this.gssInfo.a);
      } else { // 'b'
        this.computeLkhd(this.gssInfo.b);
      }
    },
    workerBusy: function(newVal, oldVal) {
      var getGssSeries = () => {
        const seq2 = Array(this.sComputed.length).fill().map((_, i) => i);
        var gssSeries = seq2.map(i => ([this.sComputed[i], this.lComputed[i]]));
        gssSeries.sort(function(a,b) {return a[0]-b[0]});
        return(gssSeries);
      }
      var findCrossing = function(lkhds0, lkhds1, thresh0) {
        const i0 = lkhds1.findIndex(x => x > thresh0)
        if (i0 >= 1) {
          const x1 = lkhds0[i0-1];
          const x2 = lkhds0[i0];
          const y1 = lkhds1[i0-1];
          const y2 = lkhds1[i0];
          return x1 + (x2-x1) / (y2-y1) * (thresh0-y1);
        } else {
          return lkhds0[0];
        }
      }
      if ((oldVal) && (!newVal)) { // worker has switched from busy to not busy
        this.sComputed.push(this.summary[0]);
        this.lComputed.push(this.summary[3]);
        if (this.summary[1] > this.nSubSteps) this.nSubSteps = this.summary[1];
        const i9 = this.lkhdt.length;
        const i0 = this.lkhdt.findIndex(x => x>0);
        const seq = [...Array(i9-i0).keys()].map((i) => (i0 + i));
        if (this.lkhdPlotDict.series != null) {
          this.lkhdPlotDict.series.push({'name': "s="+this.summary[0].toFixed(6),
            'type': 'line',
            'data': seq.map(i => ([-i * 2 * this.N0 * this.dt, this.lkhdt[i]])) });
        } else {
          this.lkhdPlotDict = {series: [{'name': "s="+this.summary[0].toFixed(6),
            'type': 'line',
            'data': seq.map(i => ([-i * 2 * this.N0 * this.dt, this.lkhdt[i]])) }]};
        }
        this.gssPlotDict = {series: [{'name': 'likelihood', 'data': getGssSeries()}]};
        if (this.task != 'mle') return;
        if (this.gssInfo.cur == 'c') { // if neither c nor d, then a or b
          this.gssInfo.yc = this.summary[3];
        } else if (this.gssInfo.cur == 'd') {
          this.gssInfo.yd = this.summary[3];
        }
        if (this.gssInfo.cur == 'b') { // first compute for upper bound, then lower bound
          this.gssInfo.cur = 'a';
          this.gssStep++;
          return;
        }
        if (this.gssInfo.yc == null) {
          this.gssInfo.cur = 'c';
          this.gssStep++;
          return;
        }
        if (this.gssInfo.yd == null) {
          this.gssInfo.cur = 'd';
          this.gssStep++;
          return;
        }
        if (this.gssStep > this.gssInfo.n) { // gss steps done
          if (this.flagAge == 1) {
            const imax = this.lComputed.indexOf(Math.max(...this.lComputed));
            const lkhdtMax = this.lkhdPlotDict.series[imax].data.map(x => x[1])
            const lmax = Math.max(...lkhdtMax);
            const kmax = lkhdtMax.indexOf(lmax);
            const d2t = (Math.log(lkhdtMax[kmax-1]) - 2 * Math.log(lkhdtMax[kmax]) + Math.log(lkhdtMax[kmax+1])
              ) / Math.pow(2 * this.N0 * this.dt, 2);
            const sigmat = 1 / Math.sqrt(-d2t);
            const l = sigmat * 1.96;
            const kStep = Math.round(l / (2 * this.N0 * this.dt));
            let kLower, kUpper;
            if (kmax <= kStep) {kLower = 0;}
            else (kLower = kmax - kStep);
            if (kmax >= lkhdtMax.length - kStep) {kUpper = lkhdtMax.length-1;}
            else {kUpper = kmax + kStep;}
            //const seq = [...Array(kUpper-kLower+1).keys()].map((i) => (kLower + i));
            var curSeries = this.lkhdPlotDict.series;
            curSeries.push({
              name: '95% Confidence interval',
              type: 'area',
              data: this.lkhdPlotDict.series[imax].data.slice(kLower, kUpper+1)});
            const t0 = this.lkhdPlotDict.series[imax].data[0][0];
            this.ageEst = {lower: -kLower * 2 * this.N0 * this.dt + t0,
              upper: -kUpper * 2 * this.N0 * this.dt + t0,
              mle: -kmax * 2 * this.N0 * this.dt + t0}
            this.lkhdPlotDict = {tmax: this.ageEst.mle, series: curSeries};
          }
          // find CI for selection coefficient
          const lkhds = this.gssPlotDict.series[0].data.map(
            (_, colIndex) => this.gssPlotDict.series[0].data.map(row => row[colIndex]));
          const lmaxs = Math.max(...lkhds[1]);
          const smax = lkhds[0][lkhds[1].indexOf(lmaxs)];
          const thresh = lmaxs * 0.058;
          const sLower = findCrossing(lkhds[0], lkhds[1], thresh);
          const lkhds0Rev = lkhds[0].slice().reverse();
          const lkhds1Rev = lkhds[1].slice().reverse();
          const sUpper = findCrossing(lkhds0Rev, lkhds1Rev, thresh);
          this.sComputed.push(sLower);
          this.lComputed.push(thresh);
          this.sComputed.push(sUpper);
          this.lComputed.push(thresh);
          const gssSeries = getGssSeries();
          const i1 = gssSeries.findIndex((x) => x[0]==sLower);
          const i9 = gssSeries.findIndex((x) => x[0]==sUpper);
          this.sEst = {lower: sLower, upper: sUpper, mle: smax};
          this.gssPlotDict = {
            series: [{'name': 'likelihood', 'type': 'line', 'data': gssSeries},
              {'name': '95% Confidence interval', 'type': 'area', 'data': gssSeries.slice(i1, i9+1)}],
            smax: smax,
          };
          return;
        }
        if (this.gssInfo.yc > this.gssInfo.yd) {
            this.gssInfo.b = this.gssInfo.d;
            this.gssInfo.d = this.gssInfo.c;
            this.gssInfo.yd = this.gssInfo.yc;
            this.gssInfo.h1 = this.gssInfo.invphi * this.gssInfo.h1;
            this.gssInfo.c = this.gssInfo.a + this.gssInfo.invphi2 * this.gssInfo.h1;
            this.gssInfo.yc = null;
            this.gssInfo.cur = 'c';
            this.gssStep++;
        } else {
            this.gssInfo.a = this.gssInfo.c;
            this.gssInfo.c = this.gssInfo.d;
            this.gssInfo.yc = this.gssInfo.yd;
            this.gssInfo.h1 = this.gssInfo.invphi * this.gssInfo.h1;
            this.gssInfo.d = this.gssInfo.a + this.gssInfo.invphi * this.gssInfo.h1;
            this.gssInfo.yd = null;
            this.gssInfo.cur = 'd';
            this.gssStep++;
        }
      }
    }
  },
  methods: {
    setTask(task) {
      this.task = task;
      //if (task=="configCurves") {this.$router.go()}
    },
    setflagAge(b) {
      this.flagAge = b;
    },
    flipShowCounts() {
      this.showCounts = !this.showCounts;
    },
    flipShowNt() {
      this.showNt = !this.showNt;
    },
    removeNt() {
      if (!(this.N0 > 0)) this.N0 = 5000;
      this.NtFile = null;
      this.Nt = [ [ 0 ], [ this.N0 ] ];
      this.genSamplePlot();
    },
    genSamplePlot() {
      if (this.counts != null) {
        const seq = Array(this.counts[0].length).fill().map((_, i) => i);
        const l = Math.max(...this.counts[0]);
        const seq2 = Array(this.Nt[0].length).fill().map((_, i) => i);
        var NtPlot = seq2.map(i => ([-this.Nt[0][i], Math.round(this.Nt[1][i])]));
        const pLast = this.Nt[1][this.Nt[1].length-1];
        if (l > this.Nt[0][this.Nt[0].length-1]) NtPlot.push([-l, pLast]);
        this.samplePlotDict = [{'name': 'mutant count',
            'type': 'column',
            'data': seq.map(i => ([-this.counts[0][i], Math.round(this.counts[2][i])]))},
          {'name': 'non-mutant count', 
            'type': 'column',
            'data': seq.map(i => ([-this.counts[0][i], Math.round(this.counts[1][i] - this.counts[2][i])]))},
          {'name': 'mutant proportion', 
            'type': 'area',
            'data': seq.map(i => ([-this.counts[0][i],
                                  Math.round(100 * this.counts[2][i] / this.counts[1][i]) / 100]))},
          {'name': 'population size',
            'type': 'line',
            'data': NtPlot,
          }];
      }
    },
    computeLkhd(sel_in) {
      if (this.task != 'mle') {
        this.lkhdPlotDict = {series: null};
      }
      this.workerBusy = true;
      this.worker.postMessage({
        type: 'COMPUTE',
        payload: { N0: this.N0, dt: this.dt, hh: this.hh, flagAge: this.flagAge, lkhdCutoffRatio: this.lkhdCutoffRatio,
          nSubSteps: this.nSubSteps, sel: sel_in, counts: this.counts},
      })
    },
    convertStrArray(str) {
      const lines1 = str.split('\n');
      var lines2 = lines1.map((s) => {return s.split(' ').map(parseFloat);})
      const nc = lines2[0].length;
      // get all lines with exact the same number of numbers as the first line
      const lines3 = lines2.filter(l => (l.length==nc));
      let columns = [];
      for (let i=0; i<nc; i++) {
        columns.push(lines3.map(l => l[i]));
      }
      return(columns);
    },
    readFile(ev, fileType) {
      let curFile;
      if (fileType == 1) {
        this.dataFile = ev.target.files[0];
        curFile = this.dataFile;
      } else {
        this.NtFile = ev.target.files[0];
        curFile = this.NtFile;
      }
      const reader = new FileReader();
      if (curFile.name.includes(".txt")) {
        reader.onload = (res) => {
          if (fileType == 1) {
            this.counts = this.convertStrArray(res.target.result);
            this.genSamplePlot();
          } else {
            this.Nt = this.convertStrArray(res.target.result);
            this.N0 = this.Nt[1][0];
            this.genSamplePlot();
          }
        };
        reader.onerror = (err) => console.log(err);
        reader.readAsText(curFile);
      } else {
        console.log("readFile: unknown file type");
      }
      this.sEst = null;
      this.ageEst = null;
      this.sComputed = [];
      this.lComputed = [];
      this.lkhdPlotDict = {series: null};
      this.gssPlotDict = {series: null};
    },
    gss() {
      this.sEst = null;
      this.ageEst = null;
      this.sComputed = [];
      this.lComputed = [];
      this.lkhdPlotDict = {series: null};
      this.gssPlotDict = {series: null};
      if (this.smin > this.smax) [this.smax, this.smin] = [this.smin, this.smax];
      let a = this.smin, b = this.smax;
      const h1 = b - a;
      const gssTol = 1e-4;
      const invphi = 0.6180339887;
      const invphi2 = 0.3819660113;
      const n = Math.ceil(Math.log(gssTol / h1) / Math.log(invphi));
      var c = a + invphi2 * h1;
      var d = a + invphi * h1;
      // computer lower bound a first, for confidence interval
      this.gssInfo = {'cur': 'b', 'a': a, 'b': b, 'c': c, 'd': d, yc: null, yd: null,
        'invphi': invphi, 'invphi2': invphi2, 'h1': h1, 'n': n};

      this.gssStep = 0;
      this.gssStep++;
    },
  }
}

</script>

